<template>
  <section class="offer__container">
    <div class="column column--left">
      <div class="offer__details">
        <h2 class="details__title">{{ offerTitle }}</h2>
        <p class="details__desc">{{ offerDesc }}</p>
      </div>
      <div class="details__BgContainer"></div>
    </div>
    <div class="column column--right">
      <div
        class="offer__itemBox"
        v-for="box of boxes"
        :key="box.title"
        @click="onOffer(box)"
      >
        <div class="itemBox__contentContainer">
          <div class="itemBox__iconContainer">
            <img :src="iconPath(box)" :alt="box.title" class="itemBox__icon" />
          </div>
          <h3 class="itemBox__title">
            {{ box.title }}
          </h3>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import contentEN from "@/locales/en.json";

export default {
  name: "Offer",
  data() {
    return {
      offerTitle: "",
      offerDesc: "",
    };
  },
  created() {
    this.offerTitle = this.initOffer.title;
    this.offerDesc = this.initOffer.desc;
  },
  computed: {
    initOffer() {
      return contentEN.content.offer.initialContent;
    },
    boxes() {
      return contentEN.content.offer.content;
    },
  },
  methods: {
    iconPath(box) {
      let fileName = box.title.split(" ").join("_");
      return require(`../assets/${fileName}.svg`);
    },
    onOffer(data) {
      this.offerTitle = data.title;
      this.offerDesc = data.desc;
    },
  },
};
</script>

<style lang="scss" scoped>
.offer__container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.column {
  &--left {
    width: 60%;
    color: #ffffff;
    position: relative;
  }
  &--right {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    column-gap: 5px;
    row-gap: 5px;
    place-items: stretch stretch;
    place-content: center center;
    width: 30%;
    color: #343434;
  }
}

.offer__details {
  width: 100%;
  padding: 80px 120px 80px 180px;
  background-color: #282828;
  position: relative;
  z-index: 1;
}

.details {
  &__title {
    font-size: 35px;
    font-weight: 600;
    margin-bottom: 40px;
    margin-top: 0;
  }

  &__desc {
    font-size: 20px;
    line-height: 1.75;
    margin: 0;
  }

  &__BgContainer {
    width: 100%;
    height: 70%;
    position: absolute;
    left: 120px;
    top: 120px;
    background-image: url("../assets/offer_bg.svg");
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 0;
  }
}

.offer__itemBox {
  position: relative;
  padding-top: 100%;
  transition: box-shadow 0.5s;
  &:hover {
    cursor: pointer;
    box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.25);
  }
}

.itemBox {
  &__contentContainer {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
  }
  &__iconContainer {
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
  }
  &__icon {
    height: 70%;
  }
  &__title {
    margin: 0;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
  }
}
</style>
